<template>
  <div class="reports">
    <h4 class="fw-bold py-3 mb-4 d-flex">
        <span class="mr-auto">
          {{$trans('Deposit')}}
        </span>
        <button @click="formFilter = !formFilter" class="btn btn-outline-primary btn-sm mr-3">
          <svg viewBox="0 0 24 24" width="16" height="16" stroke="currentColor" stroke-width="2" fill="none"
               stroke-linecap="round" stroke-linejoin="round" class="css-i6dzq1 mr-1">
            <polygon points="22 3 2 3 10 12.46 10 19 14 21 14 12.46 22 3"></polygon>
          </svg>
          {{ $trans('Filter') }}
        </button>
    </h4>
    <div v-if="formFilter" class="form-filter mb-3 card">
      <div class="card-body">
        <div class="d-flex">
          <div class="form-group mr-3">
            <label class="d-block">{{ $trans('Keyword') }}</label>
            <input v-model="filterQuery.keyword" class="form-control" type="text">
          </div>
          <div class="form-group mr-3">
            <label class="d-block">{{ $trans('Method') }}</label>
            <select v-model="filterQuery.method" class="form-control w-200">
              <option value="all">{{ $trans('All') }}</option>
              <option value="IB">{{ $trans('IB') }}</option>
              <option value="BT">{{ $trans('BT') }}</option>
              <option value="QRPAY">{{ $trans('QRPAY') }}</option>
              <option value="MOMO">{{ $trans('MOMO') }}</option>
              <option value="ZALO">{{ $trans('ZALO') }}</option>
            </select>
          </div>
          <div class="form-group mr-3">
            <label class="d-block">{{ $trans('Status') }}</label>
            <select v-model="filterQuery.status" class="form-control w-200">
              <option value="all">{{ $trans('All') }}</option>
              <option v-for="(status,k) in deposit_status" :key="k" :value="k">{{ status}}</option>
            </select>
          </div>
        </div>
        <div class="d-flex mb-3">
          <div class="form-group mr-3">
            <label class="d-block">{{ $trans('From') }}</label>
            <date-picker class="w-200" v-model="filterQuery.from"></date-picker>
          </div>
          <div class="form-group  mr-3">
            <label class="d-block">{{ $trans('To') }}</label>
            <date-picker class="w-200" v-model="filterQuery.to"></date-picker>
          </div>

        </div>
        <div class="filter-actions d-flex">
          <button @click="reset_filter" class="btn btn-outline-danger btn-sm mr-3">{{ $trans('Reset') }}</button>
          <button @click="filter" class="btn btn-outline-primary btn-sm mr-3">
            <div v-if="process_loading" class="spinner-border spinner-border-sm text-secondary"></div>
            {{ $trans('Apply') }}
          </button>
          <download-csv v-if="json_data && json_data.length" :labels="labels" :data="json_data" :name="export_file_name">
            <button class="btn btn-sm btn-primary">{{ $trans('Download') }}</button>
          </download-csv>
          <button v-else @click="export_transaction" class="btn btn-primary btn-sm mr-3">
            <div v-if="process_export_loading" class="spinner-border spinner-border-sm text-secondary"></div>
            {{ $trans('Export') }}
          </button>
        </div>
      </div>
    </div>
    <div class="card">
      <div class="card-body">
        <slot v-if="transactions && transactions.data">
          <div class="table-responsive">
            <div class="list-info mb-3">
              <small>
                {{ $trans('Results') }}:
                <strong>{{ transactions.data.length }}</strong> {{ $trans('of') }} <strong>
                {{transactions.total }}
              </strong>
              </small>
            </div>
            <table class="table table-striped">
              <thead>
              <tr>
                <th>{{ $trans('Ref Number') }}</th>
                <th>{{ $trans('Method') }}</th>
                <th>{{ $trans('Order ID') }}</th>
                <th>{{ $trans('Card') }}</th>
                <th>{{ $trans('Pin') }}</th>
                <th>{{ $trans('Serial') }}</th>
                <th class="text-right">{{ $trans('Amount') }}</th>
                <th class="text-right">{{ $trans('Real Amount') }}</th>
                <th class="text-right">{{ $trans('Fee') }}</th>
                <th>{{ $trans('Date') }}</th>
                <th>{{ $trans('Finish Time') }}</th>
                <th>{{ $trans('Time elapsed') }}</th>
                <th>{{ $trans('Status') }}</th>
                <th>{{ $trans('Note') }}</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="(transaction,index) in transactions.data" v-bind:key="index">
                <td>{{ transaction.ref_number }}</td>
                <td>
                    <span class="badge badge-success">
                      {{ transaction.method }}
                    </span>
                </td>
                <td>{{ transaction.order_id }}</td>
                <td>
                  <slot v-if="transaction.method=='THECAO'">{{ transaction.type }}</slot>
                </td>
                <td>
                  <slot v-if="transaction.method=='THECAO'">{{ transaction.pin }}</slot>
                </td>
                <td>
                  <slot v-if="transaction.method=='THECAO'">{{ transaction.serial }}</slot>
                </td>
                <td class="text-right text-success">
                  <vue-numeric
                      currency-symbol-position="suffix"
                      currency=""
                      v-bind:value="transaction.amount"
                      v-bind:read-only="true"
                      v-bind:precision="0">
                  </vue-numeric>
                </td>
                <td class="text-right text-success">
                  <vue-numeric
                      currency-symbol-position="suffix"
                      currency=""
                      v-bind:value="transaction.real_amount"
                      v-bind:read-only="true"
                      v-bind:precision="0">
                  </vue-numeric>
                </td>
                <td class="text-right text-success">
                  <vue-numeric
                      currency-symbol-position="suffix"
                      currency=""
                      v-bind:value="transaction.fee"
                      v-bind:read-only="true"
                      v-bind:precision="0">
                  </vue-numeric>
                </td>
                <td>{{ transaction.created_at }}</td>
                <td>
                  <slot v-if="transaction.finish_time">{{transaction.finish_time}}</slot>
                  <slot v-else>-</slot>
                </td>
                <td>
                  <slot v-if="transaction.time_elapsed">
                    <vue-numeric
                        currency-symbol-position="suffix"
                        currency="min"
                        v-bind:value="transaction.time_elapsed"
                        v-bind:read-only="true"
                        v-bind:precision="2">
                    </vue-numeric>
                  </slot>
                  <slot v-else>-</slot>
                </td>
                <td>
                <span v-if="transaction.status==1" class=" badge badge-success badge-pill">
                  {{ $trans('Success') }}
                </span>
                  <span v-if="transaction.status==2" class=" badge badge-warning badge-pill">
                  {{ $trans('Pending') }}
                </span>
                  <span v-if="transaction.status==3" class=" badge badge-info badge-pill">
                  {{ $trans('Processing') }}
                </span>
                  <span v-if="transaction.status==0" class=" badge badge-warning badge-pill">
                  {{ $trans('Hold') }}
                </span>
                  <span v-if="transaction.status==-1" class=" badge badge-danger badge-pill">
                  {{ $trans('Reject') }}
                </span>
                  <slot v-if="transaction.status<-1">
                    <span v-if="transaction.status==-4" class=" badge badge-dark badge-pill">
                      {{deposit_status[transaction.status] }}
                    </span>
                    <span v-else class=" badge badge-danger badge-pill">
                      {{deposit_status[transaction.status]}}
                    </span>
                  </slot>
                </td>
                <td>{{transaction.note}}</td>
              </tr>
              </tbody>
            </table>
          </div>
          <paginate v-if="transactions && transactions.data && transactions.last_page"
                    :page-count="transactions.last_page"
                    :click-handler="paginate"
                    :prev-text="$trans('Previous')"
                    :next-text="$trans('Next')"
                    :page-class="'page-item'"
                    :prev-class="'page-item'"
                    :next-class="'page-item'"
                    :page-link-class="'page-link'"
                    :prev-link-class="'page-link'"
                    :next-link-class="'page-link'"
                    :container-class="'pagination'">
          </paginate>
        </slot>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
export default {
  name: "Deposit",
  data:function (){
    return {
      transactions:null,
      filterQuery: {
        page: 1,
        from: null,
        to: new Date(),
        method: 'all',
        keyword: '',
        status:'all',
        bank:'all',
      },
      transaction:null,
      showDetail:false,
      tabActive:'detail',
      errors:{},
      process:false,
      reason:'',
      alert:null,
      formFilter:false,
      process_loading:false,
      export_data:null,
      process_export_loading:null,
      labels:{
        ref_number:'REF NUMBER',
        method:'METHOD',
        order_id:'ORDER ID',
        amount:'AMOUNT',
        real_amount:'REAL AMOUNT',
        fee:'FEE',
        date:'DATE',
        finish_time:'FINISH TIME',
        time_elapsed:'TIME ELAPSED (MIN)',
        status:'STATUS',
      },
    }
  },
  mounted() {
    var today = new Date();
    this.filterQuery.from = new Date(today.getFullYear(), today.getMonth(), 1)
  },
  methods:{
    filter: function () {
      this.filterQuery.page = 1;
      this.index()
    },
    paginate: function (pageNum) {
      this.filterQuery.page = pageNum;
      this.index();
    },
    index: function () {
      this.process_loading = true
      axios({
        url: this.$root.$data.api_url + '/api/client/deposit/transactions',
        params: this.filterQuery,
        method: 'GET'
      })
          .then(resp => {
            this.transactions = resp.data;
            this.process_loading = false
          })
          .catch(err => {
            console.log(err)
            this.process_loading = false
          })
    },
    export_transaction:function (){
      this.process_export_loading = true
      axios({
        url: this.$root.$data.api_url + '/api/client/deposit/transactions/export',
        params: this.filterQuery,
        method: 'GET'
      })
          .then(resp => {
            this.export_data = resp.data;
            this.process_export_loading = false
          })
          .catch(err => {
            console.log(err)
            this.process_export_loading = false
          })
    },
    reset_filter: function () {
      var today = new Date();
      this.filterQuery = {
        from: new Date(today.getFullYear(), today.getMonth(), 1),
        to: new Date(),
        page: 1,
        method: 'all',
        keyword: '',
        status:'all',
        bank:'all',
        client_id:null
      }
      this.export_data = null
      this.index()
    },
  },
  created() {
    this.index()
  },
  computed:{
    deposit_status: function () {
      return this.$root.$data.global_settings.deposit_status;
    },
    bank_supports: function () {
      return this.$root.$data.global_settings.bank_supports;
    },
    json_data:function (){
      let json_data =[]
      if( this.export_data){
        let export_data = this.export_data
        export_data.forEach((el)=>{
          let status = this.deposit_status[el.status]
          let  item ={
            ref_number:el.ref_number,
            method:el.method,
            order_id:el.order_id,
            amount:el.amount,
            real_amount:el.real_amount,
            fee:el.fee,
            date:el.created_at,
            finish_time:el.finish_time,
            time_elapsed:el.time_elapsed,
            status:status,
          }
          json_data.push(item)
        })
      }
      return json_data
    },
    export_file_name:function () {
      let from= ''
      let to= ''
      from = moment(this.filterQuery.from).format('YYYY-MM-DD')
      to = moment(this.filterQuery.to).format('YYYY-MM-DD')
      return 'deposit-transactions-'+from+'-'+to+'.csv';
    }
  }
}
</script>

